.works {
	padding-top: 155px;

	&__swiper {
		background: #41303B;
		transform: rotate(-2.5deg);
	}

	&__slide {
		color: $color-primary;

		&:nth-child(2n+1) {
			color: rgba(234, 42, 138, 0.2);
		}
	}

	&__desc {
		font-family: "Orbitron", sans-serif;
		font-weight: 700;
		font-size: 48px;
		line-height: 150%;
		padding: 33px 0;
	}

	&__content {
		display: flex;
		justify-content: space-between;
		margin-top: 95px;

		@media (max-width: 767px) {
			flex-direction: column;
			align-items: center;
		}
	}

	&__img {
		width: 487px;
		height: 835px;
		margin-top: 70px;

		@media (max-width: 1190px) {
			width: 397px;
			height: 675px;
		}

		@media (max-width: 991px) {
			width: 267px;
			height: 465px;
		}

		@media (max-width: 767px) {
			margin-top: 0;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__right {
		width: 48%;

		@media (max-width: 767px) {
			width: 100%;
		}
	}

	&__links {
		display: grid;
		grid-gap: 40px;

		@media (min-width: 1190px) {
			grid-template-columns: repeat(2, 1fr);
		}
	}

	&__link {
		padding: 40px 29px;
		height: 100%;

		&:hover {
			.works__text {
				color: #fff;
			}
		}
	}

	&__title {
		font-family: "Orbitron", sans-serif;
		font-weight: 700;
		font-size: 20px;
		line-height: 150%;
		color: #DDEAE9;

		@media (max-width: 991px) {
			font-size: 18px;
		}

		@media (max-width: 575px) {
			font-size: 16px;
		}
	}

	&__text {
		line-height: 150%;
		color: #AFAFAF;
		margin-top: 15px;
		transition: all .3s ease;

		@media (max-width: 991px) {
			font-size: 16px;
		}

		@media (max-width: 575px) {
			font-size: 14px;
		}
	}

	&__btn {
		margin-top: 40px;
		width: 100%;
		text-align: center;
		padding-top: 28px;
		padding-bottom: 28px;

		@media (max-width: 991px) {
			padding-top: 20px;
			padding-bottom: 20px;
		}

		@media (max-width: 575px) {
			padding-top: 15px;
			padding-bottom: 15px;
		}
	}

	.swiper-wrapper {
		transition-timing-function: linear !important;
	}
}