.footer {
	margin-top: 275px;
	padding: 60px 0;
	background: rgba(46, 18, 34, 1);

	@media (max-width: 991px) {
		margin-top: 100px;
	}

	@media (max-width: 575px) {
		padding: 35px 0;
	}

	&__wrap {
		position: relative;
	}

	&__content {
		margin-top: 60px;

		@media (max-width: 575px) {
			margin-top: 30px;
		}
	}

	&__pic {
		position: absolute;
		top: -298px;
		right: 170px;

		@media (max-width: 991px) {
			display: none;
		}
	}

	&__body {
		display: flex;

		@media (max-width: 991px) {
			flex-direction: column;
		}
	}

	&__form {}

	&__desc {
		font-family: "Orbitron", sans-serif;
		font-weight: 400;
		font-size: 24px;
		line-height: 150%;
		color: #B9B9B9;

		@media (max-width: 575px) {
			font-size: 16px;
		}
	}

	&__item {
		position: relative;
		margin-top: 30px;

		@media (max-width: 575px) {
			margin-top: 15px;
		}
	}

	&__input {
		background: #26071A;
		border: 1px solid #411831;
		box-sizing: border-box;
		border-radius: 4px;
		height: 90px;
		width: 100%;
		width: 560px;
		padding: 0 250px 0 40px;
		color: #B9B9B9;

		@media (max-width: 991px) {
			width: 100%;
		}

		@media (max-width: 575px) {
			height: 68px;
			padding: 0 150px 0 15px;
		}

		&::placeholder {
			font-size: 18px;
			line-height: 150%;
			color: #B9B9B9;

			@media (max-width: 575px) {
				font-size: 16px;
			}
		}

		&:focus {
			outline: none;
			box-shadow: 0 0 10px #411831;
		}
	}

	&__btn {
		position: absolute;
		top: 50%;
		right: 15px;
		transform: translateY(-50%);
		padding: 18px 49px;

		@media (max-width: 575px) {
			padding: 8px 25px;
		}
	}

	&__box {
		margin-left: 50px;

		@media (max-width: 991px) {
			margin: 30px 0 0;
		}
	}

	&__icons {
		margin-top: 30px;
		display: flex;

		@media (max-width: 575px) {
			margin-top: 15px;
		}
	}

	&__link {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 115px;
		height: 80px;
		background: #e52c85;
		border-radius: 8px;
		transition: all .3s ease;

		@media (max-width: 575px) {
			width: 56px;
			height: 44px;
		}

		&:not(:last-child) {
			margin-right: 23px;

			@media (max-width: 575px) {
				margin-right: 10px;
			}
		}

		&:hover {
			transform: translateY(-5px);
		}

		img {
			width: 32px;
			height: 26px;

			@media (max-width: 575px) {
				width: 20px;
				height: 20px;
			}
		}
	}

	&__bottom {
		@include jcsb;
		margin-top: 55px;

		@media (max-width: 991px) {
			flex-direction: column;
			align-items: flex-start;
		}

		@media (max-width: 575px) {
			margin-top: 30px;
		}
	}

	&__left {
		@include aic;
		font-family: "Orbitron", sans-serif;
		font-weight: 700;
		line-height: 140%;
		color: #fff;

		img {
			margin-left: 10px;
		}

		@media (max-width: 575px) {
			font-size: 14px;
		}

		@media (max-width: 395px) {
			font-size: 11px;
		}
	}

	&__right {
		@include aic;
		font-family: "Orbitron", sans-serif;
		font-weight: 700;
		line-height: 140%;
		color: #B9B9B9;

		@media (max-width: 991px) {
			margin-top: 10px;
		}

		@media (max-width: 575px) {
			font-size: 14px;
		}

		@media (max-width: 395px) {
			font-size: 11px;
		}

		img {
			margin: 0 5px;
			width: 20px;
			height: 20px;
		}

		span {
			color: #DDEAE9;
			margin-left: 5px;
		}
	}
}