  display: flex;
  flex-direction: column; }

.main {
  flex: 1 1 auto; }

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1190px; }
  @media (max-width: 1190px) {
    .container {
      max-width: 970px; } }
  @media (max-width: 991px) {
    .container {
      max-width: 750px; } }
  @media (max-width: 767px) {
    .container {
      max-width: none; } }

.title {
  font-family: "Orbitron", sans-serif;
  font-weight: 700;
  font-size: 48px;
  line-height: calc(72 / 48);
  color: #fff; }
  @media (max-width: 991px) {
    .title {
      font-size: 36px; } }
  @media (max-width: 575px) {
    .title {
      font-size: 24px; } }

.btn {
  position: relative;
  overflow: hidden;
  display: inline-block;
  border-radius: 16px;
  transition: all 0.3s ease-out;
  border: 2px solid transparent;
  background-color: #E52C85;
  font-weight: 700;
  color: #12141D;
  padding: 13px 80px;
  transform: perspective(1px) translateZ(0);
  transition-property: color;
  transition-duration: 0.4s; }
  @media (max-width: 991px) {
    .btn {
      padding: 8px 22px;
      font-size: 14px; } }
  .btn::before {
    content: "";
    position: absolute;
    z-index: -1;
    border-radius: 16px;
    background-color: #fff;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    transform: scaleX(0);
    transform-origin: 0 50%;
    transition-property: transform;
    transition-duration: 0.4s;
    transition-timing-function: ease-out; }
  @media (min-width: 992px) {
    .btn:hover:before {
      transform: scaleX(1); } }

.card {
  position: relative;
  display: block;
  border-radius: 8px;
  background: rgba(0, 0, 0, 0.12);
  transition: all 0.3s ease; }
  .card:hover {
    background: #E62C85; }
  .card::before {
    content: "";
    position: absolute;
    top: -2px;
    bottom: -2px;
    left: -2px;
    right: -2px;
    background: linear-gradient(199.22deg, rgba(255, 255, 255, 0.12) 25.85%, rgba(255, 255, 255, 0) 86.48%);
    border-radius: 8px;
    z-index: -1; }

#up {
  display: none;
  width: 54px;
  height: 54px;
  background: rgba(255, 255, 255, 0.16);
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  bottom: 30px;
  right: 30px;
  cursor: pointer;
  z-index: 1;
  opacity: 0.5;
  transition: all 0.3s ease; }
  @media (max-width: 575px) {
    #up {
      bottom: 10px;
      right: 10px; } }
  #up:hover {
    opacity: 1; }
  #up img {
    width: 16px;
    height: 16px;
    filter: invert(100%); }

