.header {
	position: absolute;
	width: 100%;
	top: -150px;
	left: 0;
	z-index: 1;
	transition: top 1.5s ease;

	&__content {
		@include jcsb;
	}

	&__left {
		display: flex;
		align-items: center;

		@media (max-width: 991px) {
			width: 100%;
		}
	}

	&__logo {
		display: flex;
		align-items: center;
		position: relative;
		z-index: 3;

		@media (max-width: 767px) {
			margin: auto;
		}

		img {
			width: 66px;
			height: 64px;
			object-fit: cover;

			@media (max-width: 991px) {
				width: 50px;
				height: 48px;
			}

			@media (max-width: 767px) {
				display: none;
			}
		}

		p {
			margin-left: 34px;
			font-family: Orbitron, sans-serif;
			font-size: 48px;
			font-weight: 700;
			color: #fff;
			text-transform: uppercase;

			@media (max-width: 991px) {
				font-size: 36px;
				margin-left: 20px;
			}
		}
	}

	&__btn {
		position: relative;
		z-index: 2;

		@media (max-width: 767px) {
			display: flex;
			align-items: center;
			justify-content: center;
			padding: 0;
			width: 40px;
			height: 40px;
			border-radius: 8px;
		}

		p {
			@media (max-width: 767px) {
				display: none;
			}
		}

		img {
			display: none;

			@media (max-width: 767px) {
				display: block;
			}
		}
	}
}

.loaded .header {
	top: 35px;
}

.menu {
	margin-right: 45px;

	@media (max-width: 991px) {
		margin: 0 auto;
	}

	&__body {
		@media (max-width: 767px) {
			background-color: #1B0412;
			position: fixed;
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
			overflow: auto;
			padding: 150px 20px 20px;
			opacity: 0;
			visibility: hidden;
			transition: all .3s ease 0s;
			text-align: center;
			z-index: 2;

			&:before {
				content: "";
				background-color: #1B0412;
				position: fixed;
				top: 0;
				left: 0;
				width: 100%;
				height: 90px;
				z-index: 50;
			}

			&.active {
				opacity: 1;
				visibility: visible;
			}
		}

		&.active {
			@media (max-width: 767px) {
				.menu__list {
					opacity: 1;
					visibility: visible;
				}
			}
		}
	}


	&__list {
		display: flex;

		@media (max-width: 767px) {
			flex-direction: column;
			margin-bottom: 30px;
		}

		&>li {

			&>a {
				position: relative;
				color: #D3E2E2;
				transition: all 0.3s ease;

				&:hover {
					color: $color-primary;
				}

				&::before {
					position: absolute;
					top: 0;
					left: 0;
					overflow: hidden;
					padding: 0 10px;
					max-height: 0;
					color: #fff;
					content: '';
					transition: max-height 0.5s;
				}

				&:hover:before {
					max-height: 100%;
				}

				@media (max-width: 991px) {
					font-size: 14px;
				}

				@media (max-width: 767px) {
					padding: 20px 0;
					display: block;
					font-size: 18px;
				}
			}
		}

		&>li+li {
			margin-left: 68px;

			@media (max-width: 1190px) {
				margin-left: 25px;
			}

			@media (max-width: 767px) {
				margin-left: 0;
			}
		}
	}

}

@media (max-width: 767px) {
	.menu__icon {
		z-index: 5;
		display: block;
		position: relative;
		width: 18px;
		height: 18px;
		cursor: pointer;
	}

	.menu__icon span,
	.menu__icon::before,
	.menu__icon::after {
		left: 0;
		position: absolute;
		height: 10%;
		transition: all 0.3s ease 0s;
		background-color: #000;
		border-radius: 10px;
	}

	.menu__icon::before {
		width: 100%;
	}

	.menu__icon span {
		width: 15px;
	}

	.menu__icon::after {
		width: 12px;
	}

	.menu__icon::before,
	.menu__icon::after {
		content: "";
	}

	.menu__icon::before {
		top: 0;
	}

	.menu__icon::after {
		bottom: 0;
	}

	.menu__icon span {
		top: 50%;
		transform: scale(1) translate(0px, -50%);
	}

	.menu__icon.active span {
		transform: scale(0) translate(0px, -50%);
	}

	.menu__icon.active::before {
		top: calc(50% - 1px);
		transform: rotate(-45deg);
	}

	.menu__icon.active::after {
		bottom: calc(50% - 1px);
		transform: rotate(45deg);
		width: 100%;
	}

	.menu__icon i {
		content: "";
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		z-index: -1;
		width: 40px;
		height: 40px;
		background: $color-primary;
		border-radius: 8px;
		transition: 0.3s ease-out;
	}
}