.roadmap {
	position: relative;
	padding-top: 250px;

	@media (max-width: 991px) {
		padding-top: 100px;
	}

	&::before {
		content: '';
		background-image: url('../img/line.png');
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		position: absolute;
		top: 451px;
		left: 0px;
		width: 100%;
		height: 3px;

		@media (max-width: 991px) {
			top: 271px;
		}

		@media (max-width: 575px) {
			top: 253px;
		}
	}

	&__content {
		display: flex;
		justify-content: space-between;
		margin-top: 45px;
	}

	&__item {
		padding: 0 5px;
	}

	&__subtitle {
		position: relative;
		font-family: "Orbitron", sans-serif;
		font-weight: 700;
		font-size: 36px;
		line-height: 150%;
		color: #fff;
		text-transform: uppercase;

		@media (max-width: 991px) {
			font-size: 26px;
		}

		&::after,
		&::before {
			content: '';
			position: absolute;
			transform: rotate(45deg);
		}

		&::after {
			top: 75px;
			left: 0px;
			width: 20px;
			height: 20px;
			background: #621E47;

			@media (max-width: 991px) {
				top: 63px;
			}
		}

		&::before {
			top: 80px;
			left: 5px;
			width: 10px;
			height: 10px;
			background: #E52C85;
			z-index: 1;

			@media (max-width: 991px) {
				top: 68px;
			}
		}
	}

	&__list {
		position: relative;
		margin-top: 80px;
		background: linear-gradient(221.46deg, rgba(255, 255, 255, 0.12) 0%, rgba(255, 255, 255, 0) 100%);
		border-radius: 8px;
		padding: 30px 25px;

		@media (max-width: 1190px) {
			min-height: 190px;
		}


		&::before {
			content: "";
			position: absolute;
			top: -2px;
			bottom: -2px;
			left: -2px;
			right: -2px;
			background: linear-gradient(199.22deg, rgba(255, 255, 255, .12) 25.85%, rgba(255, 255, 255, 0) 86.48%);
			border-radius: 8px;
			z-index: -1;
		}

		li {
			font-size: 17px;
			line-height: 150%;
			color: #DDEAE9;
			transition: all 0.3s ease;
			cursor: default;

			&:hover {
				color: $color-primary;
			}
		}

		li+li {
			margin-top: 20px;
		}
	}

	&__navigation {
		display: flex;
		align-items: center;
	}

	.swiper-wrapper {
		margin-top: 45px;
		justify-content: space-between;

		@media (max-width: 991px) {
			padding-bottom: 50px;
		}
	}

	.swiper-slide {
		@media (min-width: 991px) {
			width: 300px;
		}

		@media (min-width: 1190px) {
			width: 370px;
		}
	}

	.swiper-button-prev {
		background-image: url("../img/svg/chevron-left.svg");
	}

	.swiper-button-next {
		background-image: url("../img/svg/chevron-right.svg");
	}

	.swiper-button-next::after,
	.swiper-button-prev::after {
		content: "";
	}

	.swiper-button-prev,
	.swiper-button-next {
		position: inherit;
		background-size: 27px;
		background-position: center;
		background-repeat: no-repeat;
		transition: all 0.3s ease;
		width: 100%;
		max-width: 60px;
		height: 60px;
		background-color: rgba(255, 255, 255, 0.16);
		border-radius: 8px;
		cursor: pointer;
		z-index: 11;
		margin: 0;

		&:hover {
			background-color: $color-primary;
		}

		@media (min-width: 991px) {
			display: none;
		}

		@media (max-width: 575px) {
			max-width: 40px;
			height: 40px;
		}
	}

	.swiper-pagination-bullet {
		width: 100%;
		height: 10px;
		border-radius: 4px;

		@media (max-width: 575px) {
			height: 8px;
		}
	}

	.swiper-pagination-bullet-active {
		background: $color-primary;
	}

	.swiper-horizontal>.swiper-pagination-bullets,
	.swiper-pagination-bullets.swiper-pagination-horizontal {
		position: inherit;
		background: linear-gradient(242.04deg, rgba(255, 255, 255, 0.12) 34.67%, rgba(255, 255, 255, 0) 100%);
		border-radius: 8px;
		height: 20px;
		margin: 0 20px;
		padding: 0 5px;
		display: flex;
		align-items: center;

		@media (max-width: 575px) {
			height: 14px;
		}
	}

}