.part {
	padding-top: 171px;

	@media (max-width: 991px) {
		padding-top: 100px;
	}

	&__content {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		margin-top: 97px;

		@media (max-width: 1190px) {
			justify-content: center;
			gap: 30px;
		}

		@media (max-width: 575px) {
			margin-top: 40px;
		}
	}

	&__title {
		text-align: center;
	}

	&__item {
		flex: 0 0 160px;

		@media (max-width: 1190px) {
			flex: 0 0 305px;
		}

		@media (max-width: 575px) {
			flex: 0 0 290px;
		}
	}

	&__link {
		padding: 24px 16px;

		@media (min-width: 576px) {
			min-height: 235px;
		}

		@media (min-width: 1190px) {
			min-height: 340px;
		}

		&:hover {
			.part__text {
				color: #fff;
			}
		}
	}

	&__icon {
		width: 40px;
		height: 40px;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__subtitle {
		margin-top: 22px;
		font-family: "Orbitron", sans-serif;
		font-weight: 700;
		font-size: 16px;
		line-height: 150%;
		color: #DDEAE9
	}

	&__text {
		margin-top: 12px;
		font-size: 13px;
		line-height: 150%;
		color: #AFAFAF;
		transition: all 0.5s ease;
	}
}