.token {
	padding-top: 208px;

	@media (max-width: 991px) {
		padding-top: 100px;
	}

	&__caption {
		@include jcsb;
	}

	&__btn {
		@media (max-width: 991px) {
			width: 100%;
			margin-top: 40px;
			text-align: center;
			padding-top: 15px;
			padding-bottom: 15px;
		}
	}

	&__content {
		margin-top: 80px;
		display: flex;
		justify-content: space-between;

		@media (max-width: 991px) {
			flex-direction: column;
			align-items: center;
			margin-top: 40px;
		}
	}

	&__left {
		text-align: left;

		@media (max-width: 991px) {
			width: 40%;
		}

		@media (max-width: 767px) {
			width: 50%;
		}

		@media (max-width: 575px) {
			width: 65%;
		}
	}

	&__center {
		position: relative;

		&::before {
			content: '';
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: 624px;
			height: 624px;
			background: radial-gradient(50% 50% at 50% 50%, rgba(248, 58, 149, 0.27) 0%, rgba(248, 58, 149, 0) 100%);
		}

		@media (max-width: 991px) {
			margin-top: 30px;
			width: 250px;
			height: 250px;

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}

	&__right {
		text-align: right;

		@media (max-width: 991px) {
			text-align: left;
			width: 40%;
		}

		@media (max-width: 767px) {
			width: 50%;
		}

		@media (max-width: 575px) {
			width: 65%;
		}
	}

	&-top+&-top {
		@media (max-width: 991px) {
			margin-top: 20px;
		}
	}

	&-top {
		&__label {
			font-family: "Orbitron", sans-serif;
			font-weight: 700;
			font-size: 32px;
			line-height: 150%;
			color: #DDEAE9;

			@media (max-width: 1190px) {
				font-size: 24px;
			}

			@media (max-width: 575px) {
				font-size: 18px;
			}
		}

		&__value {
			font-family: "Orbitron", sans-serif;
			font-weight: 700;
			font-size: 32px;
			line-height: 150%;
			color: $color-primary;

			@media (max-width: 1190px) {
				font-size: 24px;
			}

			@media (max-width: 575px) {
				font-size: 18px;
			}
		}
	}

	&-body {
		margin-top: 86px;

		@media (max-width: 991px) {
			margin-top: 20px;
		}

		&__item {
			position: relative;

			&::before {
				content: '';
				position: absolute;
				background-size: 100% 100%;
				background-position: center;
				background-repeat: no-repeat;
				z-index: 1;
			}

			&--1 {
				&:before {
					bottom: -15px;
					left: 0;
					background-image: url('../img/line-left-1.png');
					width: 463px;
					height: 118px;

					@media (max-width: 1190px) {
						width: 373px;
						height: 88px;
					}

					@media (max-width: 991px) {
						background-image: url('../img/line-mob-1.png');
						width: 145px;
						height: 553px;
						top: 5px;
						left: -60px;
					}
				}
			}

			&--2 {
				margin-top: 75px;

				@media (max-width: 991px) {
					margin-top: 20px;
				}

				&:before {
					bottom: -15px;
					left: 0;
					background-image: url('../img/line-left-2.png');
					width: 590px;
					height: 150px;

					@media (max-width: 1190px) {
						width: 480px;
						height: 110px;
					}

					@media (max-width: 991px) {
						background-image: url('../img/line-mob-2.png');
						width: 221px;
						height: 417px;
						top: 5px;
						left: -50px;
					}
				}
			}

			&--3 {
				&::before {
					right: 0;
					bottom: 14px;
					background-image: url('../img/line-right-1.png');
					width: 588px;
					height: 278px;

					@media (max-width: 1190px) {
						width: 476px;
						height: 219px;
					}

					@media (max-width: 991px) {
						background-image: url('../img/line-mob-3.png');
						width: 169px;
						height: 279px;
						top: 5px;
						left: -40px;
					}
				}
			}

			&--4 {
				margin-top: 16px;

				@media (max-width: 991px) {
					margin-top: 20px;
				}

				&::before {
					right: 0;
					bottom: 43px;
					background-image: url('../img/line-right-2.png');
					width: 595px;
					height: 301px;

					@media (max-width: 1190px) {
						width: 476px;
						height: 221px;
					}

					@media (max-width: 991px) {
						background-image: url('../img/line-mob-4.png');
						width: 157px;
						height: 142px;
						top: 5px;
						left: -30px;
					}
				}
			}
		}

		&__label {
			line-height: 120%;
			color: #B9B9B9;
		}

		&__value {
			margin-top: 5px;
			font-family: "Orbitron", sans-serif;
			font-weight: 700;
			font-size: 48px;
			line-height: 120%;
			color: $color-primary;

			@media (max-width: 1190px) {
				font-size: 36px;
			}

			@media (max-width: 991px) {
				font-size: 24px;
			}

			@media (max-width: 575px) {
				font-size: 18px;
			}
		}
	}
}