.intro {
	padding: 223px 0 100px 0;
	position: relative;

	@media (max-width: 991px) {
		padding: 150px 0 0 0;
	}

	@media (min-width: 576px) {
		&::before {
			content: '';
			position: absolute;
			top: 0;
			right: 0;
			background-image: url('../img/circles-mob.png');
			background-size: contain;
			background-position: center 160px;
			background-repeat: no-repeat;
			opacity: 0;
			width: 100%;
			height: 100%;
			animation: fadeInUp 6s both;
		}
	}

	@media (min-width: 992px) {
		&::before {
			background-image: url('../img/circles-desk.png');
			background-size: cover;
			background-position: center;
		}
	}

	&__content {
		position: relative;
	}

	&__title {
		min-height: 144px;

		@media (min-width: 992px) {
			max-width: 475px;
		}

		@media (max-width: 991px) {
			min-height: auto;
		}

		@media (max-width: 575px) {
			min-height: 72px;
		}

		i {
			font-style: normal;
		}
	}

	&__desc {
		margin: 25px 0 40px 0;
		font-size: 18px;
		line-height: 150%;
		color: rgba(255, 255, 255, 0.8);

		@media (min-width: 992px) {
			max-width: 380px;
		}

		@media (max-width: 575px) {
			font-size: 14px;
		}
	}

	&__img {
		width: 574px;
		height: 662px;

		@media (min-width: 992px) {
			position: absolute;
			right: 66px;
			top: -38px;
		}

		@media (max-width: 1190px) {
			width: 444px;
			height: 502px;
			right: 0;
		}

		@media (max-width: 991px) {
			margin: 0 auto 35px;
		}

		@media (max-width: 575px) {
			width: 250px;
			height: auto;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			animation: translateY 2.5s ease infinite;
		}
	}

	@keyframes translateY {
		0% {
			transform: translateY(0)
		}

		50% {
			transform: translateY(15px)
		}
	}

	@keyframes fadeInUp {
		0% {
			opacity: 0;
			transform: translateY(-400px);
		}

		100% {
			opacity: 1;
			transform: translateY(0);
		}
	}

	&__btn {
		.btn {
			padding: 13px 66px;
		}

		@media (max-width: 991px) {
			text-align: center;
		}
	}

	&-socials {
		margin-top: 103px;
		max-width: 248px;
		display: flex;
		align-items: center;

		@media (max-width: 991px) {
			margin: 40px auto 0;
		}

		&__desc {
			font-weight: 700;
			line-height: 150%;
			color: #D3E2E2;

			@media (max-width: 575px) {
				font-size: 16px;
			}
		}

		&__icons {
			display: flex;
			align-items: center;
			margin-left: 23px;
		}

		&__link {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 50px;
			height: 50px;
			background: $color-primary;
			border-radius: 8px;
			transition: all 0.3s ease;

			&:not(:last-child) {
				margin-right: 23px;
			}

			&:hover {
				transform: translateY(5px);
			}
		}
	}
}