.tesla {
	padding-top: 230px;

	@media (max-width: 991px) {
		padding-top: 100px;
	}

	&__container {
		max-width: 1305px;
		margin: auto;
		padding: 0 15px;
	}

	&__content {
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		grid-gap: 30px;

		@media (max-width: 1305px) {
			grid-template-columns: repeat(2, 1fr);
		}
	}

	&__img {
		min-height: 265px;
		@include aic;
		justify-content: center;

		img {
			width: 79px;
			height: 94px;

			@media (max-width: 575px) {
				width: 59px;
				height: 74px;
			}
		}

		@media (max-width: 575px) {
			min-height: 150px;
		}
	}
}